//Cart.js  收藏页
import React, { useState } from 'react'
import { connect } from 'react-redux'
import store from './StoreDatas'

let sclist = store.shoucang;

const Cart = (props) => {

    return (
        <div>
            {
                props.username
                    ? <div>
                        {/* { props.username } */}
                        <h3 style={{
                            textAlign: 'center'
                        }}>这是收藏页</h3>
                        <ul>
                            {
                                sclist.map((item)=>{
                                    return <li style={{
                                        marginBottom: '10px'
                                    }}>
                                        <div style={{
                                            width: '90%',
                                            height: '80px',
                                            // backgroundColor: 'red'
                                        }}
                                        >
                                            <div style={{
                                                float: 'left',
                                                width: '70%',
                                                height: '80px',
                                                backgroundColor: 'red'
                                            }} >
                                                <img style={{
                                                    width: '100%',
                                                    height: '80px',
                                                }} src={item.img}
                                              //  ' https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg'
                                                />
                                            </div>
                                            <div style={{
                                                float: 'right',
                                                width: '17%',
                                                height: '80px',
                                                color: 'white',
                                                fontSize: '17px',
                                                textAlign: 'center',
                                                backgroundColor: 'darkorange'
                                            }}
                                                onClick={() => { }}
                                            ><p style={{
                                                marginTop: '48%',
                                            }}>删除</p></div>
                                        </div>
                                    </li>
                                })
                                
                            }
                        </ul>
                    </div>
                    : <div style={{
                        marginTop: '20%',
                        marginLeft: '36%'
                    }}>
                        <h3 >你还未登录</h3>
                        <button style={{
                            marginLeft: '8%'
                        }}
                            //onClick={ log }
                            onClick={() => { props.history.push('/Loginpage') }}
                        >登录</button>
                    </div>
            }
        </div>
    )
}
const mapStateToProps = (state) => (
    { username: state.home.username }
)
export default connect(mapStateToProps)(Cart);



