//Mytab.js
import React from 'react';
import { Button, TabBar,} from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/home',
        };
    }
    render() {
        const { pathname } = this.props.location;
        if(pathname.split('/').length > 2){
            return null;
        }

        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-home1'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-home'></i>                           
                        }
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>

                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-shoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang-copy-copy'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-liebiao'></i>
                        }
                        selectedIcon={<i className='iconfont icon-liebiao1'></i>}
                        title="订单列表"
                        key="Friend"
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')

                        } }
                    ></TabBar.Item>

                    <TabBar.Item
                        icon={<i className='iconfont icon-ziyuan'></i> }
                        selectedIcon={ <i className='iconfont icon-wodedangxuan'></i> }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/mytb' }
                        onPress={ () => {
                            this.props.history.push('/mytb')
                        } }
                    >
                        
			</TabBar.Item>
            
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);