//Search.js
import { SearchBar } from 'antd-mobile'
import React from 'react'

const Search = (props) => {
    return (
        <div>

            <div style={{
                position: 'fixed',
                width: '100%',
                top: 0,
                height: '7%',
                backgroundColor: 'white'
            }}>
                <div style={{
                    float:'left',
                    position:'relative',
                    backgroundColor:'#ddd',
                    width:'80%',
                    height:'65%',
                    marginLeft:'3.5%',
                    marginTop:'1.8%',
                    borderRadius: '4px',

                }}>
                    <select style={{
                        height:'100%',
                        backgroundColor: '#ddd',
                        borderRadius: '4px',
                        border: '0px',
                        color:'#888'
                    }}>
                        <option>宝贝</option>
                    </select>
                    <input type='text' style={{
                        height: '%',
                        backgroundColor: '#ddd',
                        border: '0px',
                    }} />
                </div>

                <div style={{
                    float:'left',
                    width:'16%',
                    height:"100%",
                    textAlign:'center',
                }}
                onClick={()=>{props.history.goBack('/home')}}>
                   <p>取消</p>
                </div>





            </div>
            这是Search!
            {/* <SearchBar></SearchBar> */}


        </div>
    )
}



export default Search;
