// App.js
import React from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import routes from './router.config';
import store from './taobao/store';
import { Provider } from 'react-redux'
import { Button, TabBar } from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import List from './taobao/List';
import Mytb from './taobao/Mytb';
import Search from './taobao/Search';
import Loginpage from './taobao/Loginpage';
import Left1 from './taobao/Left1';
// import Index from './taobao/Index';
import { render } from '@testing-library/react';



const App = () => {
    return <Provider store={store}>
     <Router >

        <Switch >
            <Route exact path='/home' component={Home} />
            <Route path='/home/search' component={Search} />
            <Route path='/Loginpage' component={Loginpage} />
            {/* <Route path='/index' component={Index} /> */}
            <Route path='/home/left1' component={Left1} />
            <Route path='/cart' component={Cart} />
            <Route path='/list' component={List} />
            <Route path='/mytb' component={Mytb} />
            {/* <Route component = { Home }/>  */}
            <Route render={() => < Redirect to='/home' />} />
        </Switch >
        <Mytab />

    </Router>
    </Provider>
}
export default App;










