//Loginpage.js  登录页
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { login } from "./actionCreators"
const Loginpage = (props) => {

    const log = () => {
        props.dispatch(login())
    }

    return (
        <div>
            {
                props.username
                    ? <div>
                        {/* { props.username } */}
                        <button
                            onClick={() => { props.history.goBack() }}
                        >
                            返回
                        </button>
                        <h2 style={{
                            textAlign: 'center'
                        }}>用户信息</h2>
                        <h3 style={{
                            textAlign: 'center'
                        }}>

                            {props.username}

                        </h3>


                    </div>
                    : <div>
                        <p style={{
                            marginTop:'10%',
                            marginLeft:'10%'
                        }}>用户名：<input type='text' /></p><br></br>
                        <p style={{
                            marginTop:'0%',
                            marginLeft:'13.5%'
                        }}>密码：<input  type='password' /></p><br></br>
                        <button style={{
                            marginTop:'0%',
                            marginLeft:'33%'
                        }}
                            onClick={log}

                        // onClick={() => { props.history.push('./Loginpage') }}
                        >点击登录</button>
                    </div>
            }
        </div>
    )
}
const mapStateToProps = (state) => (
    { username: state.home.username }
)
export default connect(mapStateToProps)(Loginpage);