//left1.js  详情页


import React, { Component } from 'react';
import { connect } from 'react-redux'
import { ondatas } from './actionCreators';
import store from './StoreDatas'

let sclist = store.shoucang;

let thisimg = JSON.stringify(localStorage.getItem('src'))
console.log(thisimg);

console.log(sclist);
class Left1 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            display2: true,
            sclist,

        }
        this.handleshow2 = this.handleshow2.bind(this)
        this.handlehide2 = this.handlehide2.bind(this)
    };


    handleChange = (e) => {
        this.setState({ inpValue: e.target.value })
    }
    componentDidMount() {
        let sclist = localStorage.getItem('sclist');
        if (sclist) {
            this.setState({
                sclist: JSON.parse(sclist)
            })
        }
    }
    componentDidUpdate() {
        localStorage.setItem(
            'sclist',
            JSON.stringify(this.state.sclist)
        )
        return true;
    }
    //
    addstate = () => {
        // this.state.sclist.push({img:this.state.inpValue})
        this.setState({
            sclist: [
                {
                    img: thisimg,
                },
                ...this.state.sclist
            ]
        })

        this.state.inpValue = "";

    }

    render() {
        const display2 = {
            position: 'absolute',
            // color: '#888',
            fontSize: '17.5px',
            marginLeft: this.state.display2 ? '2.5%' : '3%',
            marginTop: '1.3%',
            color: this.state.display2 ? '#888' : 'red'
        }
        const change = {
            incomenot: this.state.display2 ? 'iconfont icon-shoucang' : 'icon-shoucang-copy-copy'
        }

        return (

            <div>
                <div
                    className='left1gob'
                    onClick={
                        () => { this.props.history.push('/home') }
                    }
                >

                    <div style={{
                        width: '16px',
                        height: '16px',
                        marginTop: '7.5px',
                        marginLeft: '8px',
                        // backgroundColor:'red'
                    }}>
                        <i className='iconfont icon-fanhui'></i>
                        {/* </div> */}
                    </div>

                </div>

                <div>

                    <img style={{
                        width: '100%'
                    }}
                        src={localStorage.getItem('src')}
                    //  'https://img.alicdn.com/imgextra/i1/2019849029/O1CN01KERCKx2GZKd3tC7qc_!!2019849029.jpg_640x640q80_.webp'
                    />
                </div>

                <div style={{
                    width: '100%',
                    height: '40px',
                    // backgroundColor:'red',
                }}>
                    <p style={{
                        float: 'left',
                        color: '#FF4500',
                        fontSize: '16.5px',
                        marginTop: '20px',
                    }}>￥</p>
                    <p style={{
                        float: 'left',
                        color: '#FF4500',
                        fontSize: '24px',
                        marginTop: '14px'
                    }}>&nbsp;190-273</p>
                </div>
                <div style={{
                    position: 'fixed',
                    bottom: 0,
                    width: '110%',
                    height: '7.4%',
                    borderTop: '1px solid #ccc',
                    backgroundColor: 'white',

                }}
                >
                    <div style={{
                        float: 'left',
                        width: '35%',
                        height: '100%',
                        // backgroundColor: 'green',
                    }}>
                        <div style={{
                            float: 'left',
                            width: '33%',
                            height: '100%',
                            // backgroundColor: 'yellow',
                        }}
                        >
                            <i style={{
                                position: 'absolute',
                                color: 'red',
                                fontSize: '18.5px',
                                marginLeft: '3%',
                                marginTop: '1.3%'
                            }} className='iconfont icon-dianpu-tap'></i>
                            <p style={{
                                marginTop: '58%',
                                marginLeft: '20%',
                                fontSize: '14px',
                                color: '#888'
                            }}>店铺</p>
                        </div>
                        <div style={{
                            float: 'left',
                            width: '33%',
                            height: '100%',
                            // backgroundColor: 'red',
                        }}
                        >
                            <i style={{
                                position: 'absolute',
                                color: '#888',
                                fontSize: '20px',
                                marginLeft: '3%',
                                marginTop: '1%'
                            }} className='iconfont icon-taobaokefu-01'></i>
                            <p style={{
                                marginTop: '58%',
                                marginLeft: '20%',
                                fontSize: '14px',
                                color: '#888'
                            }}>客服</p>
                        </div>
                        <div>
                            {
                                this.props.username
                                    ? <div
                                        style={{
                                            float: 'left',
                                            width: '33%',
                                            height: '100%',
                                            // backgroundColor: 'blue',
                                        }}
                                        onClick={this.state.display2 ? this.handlehide2 : this.handleshow2}
                                    >
                                        <i
                                            id='star'
                                            style={display2} className='iconfont icon-shoucang'
                                            onClick={() => { this.state.display2 ? document.getElementById('star').className = 'iconfont icon-shoucang-copy-copy' : document.getElementById('star').className = 'iconfont icon-shoucang' }}

                                        ></i>
                                        <p onClick={this.addstate}
                                            style={{
                                                marginTop: '58%',
                                                marginLeft: '20%',
                                                fontSize: '14px',
                                                color: '#888'
                                            }}>收藏</p>

                                    </div>
                                    : <div
                                        style={{
                                            float: 'left',
                                            width: '33%',
                                            height: '100%',
                                            // backgroundColor: 'blue',
                                        }}
                                        onClick={() => { this.props.history.push('/Loginpage') }}


                                    >
                                        <i style={{
                                            position: 'absolute',
                                            color: '#888',
                                            fontSize: '17.5px',
                                            marginLeft: '2.5%',
                                            marginTop: '1.3%',
                                        }}
                                            id='star' style={display2} className='iconfont icon-shoucang'></i>
                                        <p style={{
                                            marginTop: '58%',
                                            marginLeft: '20%',
                                            fontSize: '14px',
                                            color: '#888'
                                        }}>收藏</p>
                                    </div>
                            }
                        </div>



                    </div>
                    <div style={{
                        float: 'left',
                        width: '64%',
                        height: '100%',
                        // backgroundColor:'red',
                        color: 'white',
                    }}>
                        <div className='jiarugwc'
                            // onClick={}
                            style={{
                                float: 'left',
                                height: '77%',
                                width: '41%',
                                marginTop: '2%',
                                // fontSize:'9px',
                                backgroundColor: '#FFC900',
                                borderTopLeftRadius: '25% 60%',
                                borderBottomLeftRadius: '25% 60%',

                            }}>
                            <p style={{
                                marginTop: '10%',
                                marginLeft: '18%',
                                fontWeight: '900'
                            }}>加入购物车</p>
                        </div>
                        <div className='lijigm'
                            // onClick={}
                            style={{
                                float: 'left',
                                height: '77%',
                                width: '43%',
                                marginTop: '2%',
                                backgroundColor: '#FF6A00',
                                borderTopRightRadius: '25% 60%',
                                borderBottomRightRadius: '25% 60%',
                            }}>
                            <p style={{
                                marginTop: '10%',
                                marginLeft: '18%',
                                fontWeight: '900',

                            }}>立即购买</p>
                        </div>

                    </div>

                </div>


                {/* <Fragment>
                    <i style={display2} className='iconfont icon-shoucang' onClick={this.state.display2 ? this.handlehide2 : this.handleshow2}></i>
                </Fragment> */}
            </div>
        )
    }
    handleshow2() {
        this.setState({
            display2: true
        })
    }
    handlehide2() {
        this.setState({
            display2: false
        })
    }

}


const mapStateToProps = (state) => (
    { username: state.home.username }

)
export default connect(mapStateToProps)(Left1, { sclist });

// export default List
