//Home.js  首页
import React from 'react'
import { Carousl, SearchBar, Carousel } from 'antd-mobile';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Search from './Search';
import { history } from 'react-router-dom'
import { connect } from 'react-redux'
import './home.css';
import { ondatas } from './actionCreators';



const Home = (props) => {

    props.dispatch(ondatas());

    return (

        <div >
            <div className='searchbar' >
                <SearchBar placeholder='寻找baobei店铺'
                    onFocus={() => { props.history.push('./home/search') }}


                />
            </div >

            <div className='carousel' >

                <Carousel autoplay={true} infinite >
                    <img style={{
                        height: '170px'
                    }}
                        src=
                        'https://gw.alicdn.com/imgextra/i4/67/O1CN01yPXGic1CMjAP1OVdX_!!67-0-lubanu.jpg'
                    />
                    <img style={
                        { height: '170px' }
                    }
                        src='
                        https://gw.alicdn.com/imgextra/i4/117/O1CN01w8hBHE1CjcyBxK7WT_!!117-0-lubanu.jpg
                        ' />
                    <img style={
                        { height: '170px' }
                    }
                        src='
                        https://gw.alicdn.com/imgextra/i1/53/O1CN01GanI2E1CGJcfKOxML_!!53-0-lubanu.jpg
                        ' />
                    <img style={
                        { height: '170px' }
                    }
                        src='
                        https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i4/6000000003172/O1CN01ZWE1x71ZIowtKsQod_!!6000000003172-0-octopus.jpg
                        ' />
                    <img style={
                        { height: '170px' }
                    }
                        src='
                        https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg
                        ' />
                </Carousel >
            </div>

            <div className='boun' >
                <h3 >-- - 你一定加肯定还喜欢-- -</h3>
            </div >

            <div className='lists' >
                {
                    props.data.map((item) => {
                        return <div className='left1'
                            onClick={() => { props.history.push('/home/left1',localStorage.setItem('src',item.img)) }}
                        >
                            <img style={
                                { width: '100%', height: '160px' }
                            } src={item.img} alt='' />
                            <p style={{
                                textAlign: 'center',
                                fontSize: '14px',
                            }}>{item.title}</p>
                            <p style={{
                                float: 'left',
                                fontSize: '15px',
                                color: 'darkorange',
                                marginTop: '-4px'
                            }}>￥ 134</p>
                            <p style={{
                                float: 'left',
                                fontSize: '14px',
                                color: '#999999',
                                marginTop: '-3px',
                                marginLeft: '10px'
                            }}>397人已购买</p>
                        </div>
                    })
                }

            </div >

        </div>
    )
}

const mapStateToProps = (state) => (
    { data: state.home.data }
)
export default connect(mapStateToProps)(Home);
// export default Home